                }).catch(e => {
                    self.$toast.error("WebHock" + e.message);
                })
            },
            clearConfig(){
                let self = this;
                self.$axios.delete("/admin/notify/webhook").then(res => {
                    self.$toast.success('æ¸é¤æåï¼');
