                    self.content = "";
                }).catch(e => {
                    self.$toast.error("email" + e.message);
                });
            },
            execConfig(uri) {
                let self = this;
                let config = {address: self.address, token: self.token, content: self.content};
                self.$axios.post(uri, {name: "webhook", config: JSON.stringify(config)}).then(res => {
                    self.$toast.success('æåï¼');
